import {memo, useCallback, useEffect} from 'react';
import {goBack, useNavigationParams, useBoolean} from '@true/core';
import {Keyboard} from 'react-native';
import {PhotoViewModal} from '@true/ui';
import {getStatusBarHeight} from 'react-native-iphone-x-helper';
import * as React from 'react';

export interface PhotoViewScreenProps {
  initialIndex: number;
  images: string[];
}

export const PhotoViewScreen = memo(() => {
  const {initialIndex, images} = useNavigationParams<PhotoViewScreenProps>();
  const [isVisible, , hide] = useBoolean(true);

  useEffect(() => {
    Keyboard.dismiss();
  }, []);

  const onClose = useCallback(() => {
    goBack();
    hide();
  }, [hide]);
  // return null;

  return (
    <PhotoViewModal
      {...{
        isVisible,
        onCloseRequest: onClose,
        useNativeDriver: true,
        coverScreen: false,
        hideModalContentWhileAnimating: true,
        images,
        initialIndex,
        style: {
          paddingTop: getStatusBarHeight(),
        },
      }}
    />
  );
});
